.nav_bar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 75px;
    background-color: $color-1;
    color: $textColor ;

    .logo {
        font-size: 30px;
    }

    .navlinks {
        display: flex;
        justify-content: flex-end;
        list-style: none;
        width: 85%;
    }

    .navlink {
        color: $textColor; 
        cursor: pointer;
    }

    .signup {
        border: 1px solid yellow;
        border-radius: 5px;
    }
    
    .mobile-menu-icon {
        display: none;
    }

    @media screen and (max-width: 780px) {
        .logo {
            display: flex;
            position: absolute;
            top: 15px;
            left: 35px;
        }

        .navlinks {
            display: none;
        }

        .navlinks-mobile {
            position: absolute;
            display: block;
            list-style: none;
            background-color: #005469;
            left: 0;
            top: 75px;
            transition: all 0.5s ease-out;
            width: 100%;
        }

        .navlink {
            color: white;
            text-align: center;
            padding: 32px;
            width: 100%;
            transition: all 0.5s ease;
        }

        .signup {
            border: none;
        }

        .mobile-menu-icon {
            display: block;
            position: absolute;
            font-size: 30px;
            color: white;
            background-color: transparent;
            border: none;
            outline: none;
            top: 15px;
            right: 25px;
            cursor: pointer;
        }
    }


    
    


}



    

